
import { defineComponent, PropType } from "vue";
import { DtColumn } from "../../types";
import { reverseSortDirection } from "../../helpers";
import { APISortOptions } from "@/mock/types";

export default defineComponent({
  inject: {
    dtContext: { default: () => ({}) },
  },
  props: {
    column: {
      type: Object as PropType<DtColumn>,
      required: true,
    },
  },
  methods: {
    handleSort() {
      const { key, sort } = this.column;

      (this as any).dtContext.$emit("sort", {
        direction: reverseSortDirection(sort),
        key,
      } as APISortOptions);
    },
  },
});
