<template>
  <header><h1>Table</h1></header>
  <div class="table-wrapper">
    <div class="table-cover">
      <data-table :columns="columns" :records="records" @sort="handleSort" />
    </div>
  </div>
  <footer><p>Footer</p></footer>
</template>

<script lang="ts">
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";

import { DataTable } from "@/components";
import { APISortOptions } from "./mock/types";

export default defineComponent({
  name: "App",
  components: { DataTable },
  setup() {
    const store = useStore();
    store.dispatch("fetchRecords");

    return {
      records: computed(() => store.getters.records),
      columns: computed(() => store.getters.columns),
      handleSort: (sort: APISortOptions) =>
        store.dispatch("fetchRecords", { sort }),
    };
  },
});
</script>

<style lang="scss">
@import "./assets/styles";

.table-wrapper {
  flex: 1;
  position: relative;

  > .table-cover {
    @include cover;

    overflow: auto;
    background: var(--surface);
  }
}
</style>
