
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";

import { DataTable } from "@/components";
import { APISortOptions } from "./mock/types";

export default defineComponent({
  name: "App",
  components: { DataTable },
  setup() {
    const store = useStore();
    store.dispatch("fetchRecords");

    return {
      records: computed(() => store.getters.records),
      columns: computed(() => store.getters.columns),
      handleSort: (sort: APISortOptions) =>
        store.dispatch("fetchRecords", { sort }),
    };
  },
});
