import { DtRecord, SortDirection } from "@/components/DataTable/types";
import { APISortOptions } from "../types";

export function sortRecords({ key, direction }: APISortOptions) {
  return (a: DtRecord, b: DtRecord): number =>
    direction === SortDirection.ASCENDING
      ? a[key].localeCompare(b[key])
      : b[key].localeCompare(a[key]);
}
