import { createStore } from "vuex";
import { API } from "@/mock";
import { APIOptions, APISortOptions } from "@/mock/types";

import { DtColumn, DtRecord } from "@/components/DataTable/types";
import { LoadingStatus } from "./types";

let controller: AbortController;

export default createStore({
  state: {
    pageNumber: 1,
    pageSize: 25,
    loadingStatus: LoadingStatus.NOT_LOADING,
    records: [] as DtRecord[],
    columns: [
      { key: "company", title: "Company" },
      { key: "contact", title: "Contact" },
      { key: "email", title: "Email" },
      { key: "phone", title: "Phone" },
      { key: "country", title: "Country" },
      { key: "city", title: "City" },
      { key: "street", title: "Street" },
    ] as DtColumn[],
  },
  mutations: {
    SET_LOADING_STATUS(state, status: LoadingStatus) {
      state.loadingStatus = status;
    },
    SET_RECORDS(state, records: DtRecord[]) {
      state.records = records;
    },
    SET_COLUMN_SORT(state, { key, direction }: APISortOptions) {
      state.columns.forEach((column) => {
        if (column.key === key) {
          column.sort = direction;
        } else {
          delete column.sort;
        }
      });
    },
  },
  actions: {
    async fetchRecords({ state, commit }, { sort } = {}) {
      if (controller) controller.abort();
      controller = new AbortController();

      const { pageNumber, pageSize } = state;
      const options: APIOptions = {};
      const { signal } = controller;

      if (sort) commit("SET_COLUMN_SORT", (options.sort = sort));
      commit("SET_LOADING_STATUS", LoadingStatus.LOADING);
      commit(
        "SET_RECORDS",
        (await API.load(pageNumber, pageSize, options, signal)).records
      );
      commit("SET_LOADING_STATUS", LoadingStatus.NOT_LOADING);
    },
  },
  getters: {
    records(state) {
      return state.records;
    },
    columns(state) {
      return state.columns;
    },
  },
  modules: {},
});
