import mockData from "./mockData.json";

import { DtRecord } from "@/components/DataTable/types";

import { groupRecords, sortRecords } from "./helpers";
import { APIOptions, APIResponse } from "./types";

const latency = () => Math.random() * 500 + 300; // simulate network latency of 300-800ms

export const API = {
  load: async (
    pageNumber = 1,
    pageSize = 25,
    { groupBy, sort }: APIOptions = {},
    signal?: AbortSignal
  ): Promise<APIResponse> =>
    new Promise((resolve) => {
      if (signal && signal.aborted) {
        return Promise.reject(new DOMException("Aborted", "AbortError"));
      }

      const offset = (pageNumber - 1) * pageSize;
      const auxiliary = {} as { groupBy?: string };
      let records = mockData as DtRecord[];

      if (sort) {
        records.sort(sortRecords(sort));
      }

      if (groupBy) {
        records = groupRecords(records, (auxiliary.groupBy = groupBy));
      }

      const data = {
        ...auxiliary,
        totalRecords: records.length,
        pageNumber,
        pageSize,
        records: records.slice(offset, offset + pageSize),
      };

      const timeout = setTimeout(() => resolve(data), latency());
      if (signal) {
        signal.addEventListener("abort", () => {
          clearTimeout(timeout);

          Promise.reject(new DOMException("Aborted", "AbortError"));
        });
      }
    }),
};
